<template>
  <div>
    <h1 class="">{{ mapel.name }}</h1>
    <p class="grey--text">@{{ mapel.username }}</p>
    <v-expansion-panels>
      <v-expansion-panel v-for="(bab, i) in mapel.bab" :key="i">
        <v-expansion-panel-header>
          {{ bab.babTitle }}
        </v-expansion-panel-header>
        <v-expansion-panel-content>
          <v-list v-if="bab.materi.length > 0">
            <v-list-group
              no-action
              prepend-icon="mdi-school"
              v-for="(materi, j) in bab.materi"
              :key="j"
            >
              <template v-slot:activator>
                <v-list-item-content>
                  <v-list-item-title
                    v-text="materi.materiTitle"
                  ></v-list-item-title>
                </v-list-item-content>
              </template>

              <v-list-item
                dense
                link
                v-for="(subMateri, k) in materi.subMateri"
                :key="k"
                :to="
                  subMateri.type === 'materi'
                    ? `/materi/${$route.params.mapel}/${subMateri._id}`
                    : `/quiz/${$route.params.mapel}/${subMateri._id}`
                "
              >
                <v-list-item-title v-text="subMateri.title"></v-list-item-title>
                <v-list-item-icon v-if="completed.includes(subMateri._id)">
                  <v-icon small color="primary">mdi-check-bold</v-icon>
                </v-list-item-icon>
              </v-list-item>
            </v-list-group>
          </v-list>
          <v-alert
            v-else
            border="bottom"
            dark
            class="text-center"
            color="pink lighten-1"
          >
            Belum ada materi untuk BAB ini
          </v-alert>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
  </div>
</template>

<script>
export default {
  props: ["allMapel", "completed"],
  computed: {
    mapel() {
      return this.allMapel.filter(
        (mapel) => mapel.name == this.$route.params.mapel.split("-").join(" ")
      )[0];
    },
  },
};
</script>

